export const getDateRange = (dateRangeType) => {
    const endDate = new Date();
    const startDate = new Date();
    let dateRange;
    switch (dateRangeType) {
        case "previous_month_to_date" /* DateRangeType.previousMonthToDate */:
            startDate.setDate(1); // Required to obtain correct month
            startDate.setMonth(startDate.getMonth() - 1); // Note: Must include previous and current month
            dateRange = {
                end_date: format(endDate, 'yyyy-MM-dd'),
                start_date: format(startDate, 'yyyy-MM-dd'),
            };
            break;
        case "last_ninety_days" /* DateRangeType.lastNinetyDays */:
            dateRange = getLast90DaysDate();
            break;
        case "last_sixty_days" /* DateRangeType.lastSixtyDays */:
            dateRange = getLast60DaysDate();
            break;
        case "last_thirty_days" /* DateRangeType.lastThirtyDays */:
            dateRange = getLast30DaysDate();
            break;
        case "current_month_to_date" /* DateRangeType.currentMonthToDate */:
        default:
            dateRange = getCurrentMonthDate();
            break;
    }
    return dateRange;
};
export const getDateRangeDefault = (queryFromRoute) => {
    return queryFromRoute.dateRange || "current_month_to_date" /* DateRangeType.currentMonthToDate */;
};
export const getPerspectiveDefault = ({ awsProviders, azureProviders, ociProviders, gcpProviders, ibmProviders, ocpProviders, queryFromRoute, userAccess, }) => {
    const perspective = queryFromRoute.perspective;
    // Upon page refresh, perspective param takes precedence
    switch (perspective) {
        case "aws" /* PerspectiveType.aws */:
        case "aws_ocp" /* PerspectiveType.awsOcp */:
        case "azure" /* PerspectiveType.azure */:
        case "azure_ocp" /* PerspectiveType.azureOcp */:
        case "gcp" /* PerspectiveType.gcp */:
        case "gcp_ocp" /* PerspectiveType.gcpOcp */:
        case "ibm" /* PerspectiveType.ibm */:
        case "ibm_ocp" /* PerspectiveType.ibmOcp */:
        case "oci" /* PerspectiveType.oci */:
        case "ocp_cloud" /* PerspectiveType.ocpCloud */:
            return perspective;
    }
    if (isOcpAvailable(userAccess, ocpProviders)) {
        return "ocp" /* PerspectiveType.ocp */;
    }
    const hasAwsCloud = hasAwsAccess(userAccess) && hasCloudProvider(awsProviders, ocpProviders);
    const hasAzureCloud = hasAzureAccess(userAccess) && hasCloudProvider(azureProviders, ocpProviders);
    const hasGcpCloud = hasGcpAccess(userAccess) && hasCloudProvider(gcpProviders, ocpProviders);
    const hasIbmCloud = hasIbmAccess(userAccess) && hasCloudProvider(ibmProviders, ocpProviders);
    if (hasAwsCloud || hasAzureCloud || hasGcpCloud || hasIbmCloud) {
        return "ocp_cloud" /* PerspectiveType.ocpCloud */;
    }
    if (isAwsAvailable(userAccess, awsProviders)) {
        return "aws" /* PerspectiveType.aws */;
    }
    if (isAzureAvailable(userAccess, azureProviders)) {
        return "azure" /* PerspectiveType.azure */;
    }
    if (isGcpAvailable(userAccess, gcpProviders)) {
        return "gcp" /* PerspectiveType.gcp */;
    }
    if (isIbmAvailable(userAccess, ibmProviders)) {
        return "ibm" /* PerspectiveType.ibm */;
    }
    if (isOciAvailable(userAccess, ociProviders)) {
        return "oci" /* PerspectiveType.oci */;
    }
    return undefined;
};
